

























import { Component, Vue, Prop } from 'vue-property-decorator'
import Page from '@/components/common-components/page.vue'
import pageView from '@/components/common-ui/page-view.vue'
@Component({
  components: {
    Page,
    pageView
  }
})
export default class Home extends Vue {
  $createToast : any
  $createDialog: any
  carList = []
  // 声明周期钩子
  page = 1
  limit = 10
  count = 0
  fleetId : any
  title: any
  showMore = true
  created () {
    this.carList = []
    this.fleetId = this.$route.query.fleetId
    this.title = this.$route.query.title
    this.getCarList({})
  }
  @Prop({ default: 'default value' }) readonly propB!: string

  // 计算属性
  // get showMore () {
  //   return Math.ceil(this.count / this.limit) >= (this.page + 1)
  // }
  getCarList (obj : any) {
    let skip = obj.skip || 0
    let limit = obj.limit || 10
    let fleetId = this.fleetId
    const toast = this['$createToast']({
      time: 0,
      txt: '加载...'
    })
    toast.show()
    this.$store.dispatch('getLineListByFleetId', { fleetId: fleetId, skip: skip, limit: limit }).then((response) => {
      if (response.success) {
        if (response.data.list.length < 10) {
          this.showMore = false
        } else {
          this.showMore = true
        }
        this.carList = this.carList.concat(response.data.list)
      }
      toast.hide()
    })
  }
  pageTurning () {
    if (!this.showMore) {
      return
    }
    this.page++
    let skip = (this.page - 1) * this.limit
    this.getCarList({ skip: skip })
  }

  editPrice (lineInfo: any) {
    let currentDtPlace = lineInfo.price
    this.$createDialog({
      type: 'prompt',
      title: `${lineInfo.start}-${lineInfo.end}`,
      prompt: {
        value: currentDtPlace,
        placeholder: '请输入'
      },
      onConfirm: (e: any, promptValue: any) => {
        if (promptValue === currentDtPlace) {
          return
        }
        const price = Number(promptValue)
        if (isNaN(price) || price <= 0) {
          const toast = this['$createToast']({
            time: 3000,
            txt: '请输入正确的价格',
            type: 'txt'
          })
          toast.show()
          return
        }
        this.updateLineInfoByQuery({
          fleet: lineInfo.fleet,
          id: lineInfo.id
        }, {
          price: price
        }, () => {
          let updateList = this.carList.map((item: any) => {
            if (item.id === lineInfo.id) {
              item.price = price
            }
            return item
          })
          this.$set(this, 'carList', updateList)
        })
      }
    }).show()
  }

  editDeparturePlace (lineInfo : any) {
    let currentDtPlace = lineInfo.departurePlace
    this.$createDialog({
      type: 'prompt',
      title: `${lineInfo.start}-发车点`,
      prompt: {
        value: currentDtPlace,
        placeholder: '请输入'
      },
      onConfirm: (e: any, promptValue: any) => {
        if (promptValue === currentDtPlace) {
          return
        }
        this.updateLineInfoByQuery({
          fleet: lineInfo.fleet,
          start: lineInfo.start
        }, {
          departurePlace: promptValue
        }, () => {
          let updateList = this.carList.map((item: any) => {
            if (item.start === lineInfo.start) {
              item.departurePlace = promptValue
            }
            return item
          })
          this.$set(this, 'carList', updateList)
        })
      }
    }).show()
  }

  updateLineInfoByQuery (query: any, set: any, callback: any) {
    this.$store.dispatch('updateLineInfoByQuery', {
      query: query,
      set: set
    }).then((response) => {
      if (response.success) {
        const toast = this['$createToast']({
          time: 3000,
          txt: '更新成功',
          type: 'txt'
        })
        toast.show()
        callback()
      }
    })
  }
}
